import { FC } from "react";
import { useTranslation } from "react-i18next";
import classes from "./DisplayFloor.module.scss";
import { Button, Card, Space, Table, TableColumnsType } from "antd";
import { PLACE_STATUS_COLORS } from "constants/ui";
import { ExpandAltOutlined } from "@ant-design/icons";
import { Svg } from "components/shared";

const DUMMY_DATA = new Array(2).fill({}).map((_, idx) => ({ id: String(idx + 1 * Math.random()).slice(-2) }));

type TableRecordDto = any;

const DisplayFloor: FC = () => {
	const { t } = useTranslation();

	const dataColumns = DUMMY_DATA.map(({ id }, idx) => {
		return {
			title: (
				<div className="f-center">
					<div className={classes.numLabel}>
						<span>{idx + 1}</span> <p>{t("ПОДЪЕЗД")}</p>
					</div>
				</div>
			),
			align: "center",
			render() {
				return (
					<Space size="middle">
						<Button
							shape="circle"
							size="middle"
							className={classes.circleApt}
							style={{
								background: Math.trunc(+id * Math.random()) > 60 ? PLACE_STATUS_COLORS.RESIDENCE : PLACE_STATUS_COLORS.NON_RESIDENCE
							}}>
							{Math.trunc(+id * Math.random())}
						</Button>
						<Button
							shape="circle"
							size="middle"
							className={classes.circleApt}
							style={{
								background: Math.trunc(+id * Math.random()) > 60 ? PLACE_STATUS_COLORS.NON_RESIDENCE : PLACE_STATUS_COLORS.RESIDENCE
							}}>
							{Math.trunc(+id * Math.random())}
						</Button>
						<Button
							shape="circle"
							size="middle"
							className={classes.circleApt}
							style={{
								background: Math.trunc(+id * Math.random()) > 60 ? PLACE_STATUS_COLORS.RESIDENCE : PLACE_STATUS_COLORS.NON_RESIDENCE
							}}>
							{Math.trunc(+id * Math.random())}
						</Button>
					</Space>
				);
			}
		};
	}) as TableColumnsType<TableRecordDto>;

	const columns: TableColumnsType<TableRecordDto> = [
		{
			title: (
				<div className={classes.numLabel}>
					<p className="mb-0">{t("Қават")}</p>
				</div>
			),
			render: (_, __, idx) => (
				<div className="f-center">
					<div className={classes.numLabel}>
						<span>{7 - idx}</span>
					</div>
				</div>
			),
			width: 50,
			align: "center"
		},
		...dataColumns
	];

	return (
		<Card className={classes.card}>
			<div className="d-f jc-e mb-10">
				<Button size="large" icon={<Svg icon="openFull" width={30} height={30} />} className="f-center" disabled />
			</div>
			<Table size="small" bordered columns={columns} dataSource={new Array(7).fill({})} pagination={false} />
		</Card>
	);
};

export default DisplayFloor;
