import { FC } from "react";
import { useTranslation } from "react-i18next";
import classes from "./CreateApt.module.scss";
import { Button, Card, Checkbox, Col, Row, Select, Space, Tooltip } from "antd";
import { PlusCircleOutlined } from "@ant-design/icons";
import { AddFloor, DisplayFloor } from "./components";

const DUMMY_ENTRANCE_DATA = new Array(4).fill(null);
const DUMMY_OPTIONS = DUMMY_ENTRANCE_DATA.map((_, idx) => ({ label: idx + 1, value: idx + 1 }));

const CreateApt: FC = () => {
	const { t } = useTranslation();

	return (
		<div className={classes.wrapper}>
			<h2 className="bold mb-40">{t("Хонадон яратиш")}</h2>
			<Row gutter={[30, 30]} align="middle" className={classes.tabRow}>
				<Col xs={24} lg={12}>
					<Space size="large" className={classes.entranceTabs}>
						{DUMMY_ENTRANCE_DATA.map((_, idx) => (
							<Button size="small" type="link" key={idx}>
								{idx + 1}-{t("подъезд")}
							</Button>
						))}
					</Space>
				</Col>
				<Col xs={24} lg={12} className="text-right">
					<Space size="large">
						<Select size="large" style={{ width: 100 }} options={DUMMY_OPTIONS} className={classes.singleSelect} />
						<div className="fz-25">=</div>
						<Select size="large" style={{ width: 200 }} options={DUMMY_OPTIONS} mode="multiple" />

						<Button size="large" shape="round" type="primary">
							{t("Тасдиқлаш")}
						</Button>
					</Space>
				</Col>
			</Row>

			<Space className="mt-30 mb-30" size="large">
				<Button size="large" shape="round" icon={<PlusCircleOutlined />} type="primary">
					{t("Қават қўшиш")}
				</Button>
				<Tooltip title={t("Қўшиладиган қаватни олдинги қават билан бир хил қилиш")}>
					<Checkbox>{t("Қават бир хил")}</Checkbox>
				</Tooltip>
			</Space>

			<Row gutter={[30, 30]}>
				<Col xs={24} lg={12}>
					<AddFloor />
				</Col>
				<Col xs={24} lg={12}>
					<DisplayFloor />
				</Col>
			</Row>
		</div>
	);
};

export default CreateApt;
