import { FC } from "react";
import { useTranslation } from "react-i18next";
import classes from "../../CreateApartment.module.scss";

import {
	Button,
	Card,
	Col,
	Input,
	Row,
	Select,
	Space,
	Table,
	TableColumnsType,
	Upload
} from "antd";
import { PaperClipOutlined, DownCircleOutlined } from "@ant-design/icons";
import { Svg, Divider } from "components/shared";
import { useModal } from "components";

type TableRecordDto = any;

const Residence: FC = () => {
	const { t } = useTranslation();
	const { openModal } = useModal();

	const columns: TableColumnsType<TableRecordDto> = [
		{
			title: "№",
			render: (_, __, idx) => idx + 1,
			align: "center"
		},
		{
			title: t("Хонадон"),
			dataIndex: ""
		},
		{
			title: t("Майдони (м.кв)"),
			align: "center",
			dataIndex: ""
		},
		{
			title: t("Уй режаси"),
			align: "center",
			dataIndex: ""
		},
		{
			title: t("Таркиби"),
			align: "center",
			dataIndex: ""
		},
		{
			title: t("Амаллар"),
			align: "center",
			dataIndex: ""
		}
	];

	return (
		<>
			<Card className="pt-30 mb-42">
				<h2 className="gray-color fz-22">{t("Турар жой")}</h2>
				<Divider />
				<Row className={classes.rowPlace} justify="space-between" gutter={[30, 30]}>
					<Col sm={12} md={10} lg={6}>
						<p className="primary-color mb-10">{t("Хонадон")}</p>
						<Select size="large" placeholder="1-хона" className="w100" />
					</Col>
					<Col sm={12} md={10} lg={7}>
						<p className="primary-color mb-10">
							{t("Майдони")} <i>{t("(м.кв)")}</i>
						</p>
						<Input size="large" />
					</Col>
					<Col sm={12} md={10} lg={7}>
						<p className="primary-color mb-10">{t("Уй режаси")}</p>
						<Upload className="w100" beforeUpload={() => false} maxCount={1}>
							<Button
								size="large"
								className="w100"
								icon={<PaperClipOutlined />}
							>
								{t("Файл")}
							</Button>
						</Upload>
					</Col>
					<Col sm={12} md={10} lg={4}>
						<p className="primary-color mb-10">{t("Таркиби")}</p>
						<Space className="d-f jc-e">
							<div className="fz-20">8</div>
							<Button
								size="large"
								type="link"
								icon={<Svg icon="enterCircle" />}
								onClick={() => openModal("add-composition", {})}
							/>
						</Space>
					</Col>
				</Row>

				<Row className="mt-40" justify="center">
					<Button size="large" type="primary" shape="round">
						{t("Сақлаш")}
						<DownCircleOutlined />
					</Button>
				</Row>
			</Card>

			<Table bordered loading={false} columns={columns} dataSource={[]} />
		</>
	);
};

export default Residence;
