import { FC } from "react";
import { useTranslation } from "react-i18next";
import classes from "./AddFloor.module.scss";
import {
	Avatar,
	Button,
	Card,
	Col,
	Empty,
	Input,
	Row,
	Select,
	Space,
	Switch,
	Tooltip
} from "antd";
import { Divider, StatusDots, StatusDotsProps, Svg } from "components/shared";
import { PlusCircleFilled, DownCircleOutlined } from "@ant-design/icons";
import { PLACE_STATUS_COLORS } from "constants/ui";
import { Controller, useFieldArray, useForm } from "react-hook-form";

type FormValues = {
	places: {
		num?: string;
		type?: boolean;
		paint?: string;
	}[];
};

const AddFloor: FC = () => {
	const { t } = useTranslation();

	const { handleSubmit, control } = useForm<FormValues>({
		defaultValues: {
			places: [{}, {}, {}, {}]
		}
	});

	const { append, fields, remove } = useFieldArray({
		name: "places",
		control
	});

	const statusItems: StatusDotsProps["items"] = [
		{
			title: t("Турар жой"),
			color: PLACE_STATUS_COLORS.RESIDENCE
		},
		{
			title: t("Нотурар жой"),
			color: PLACE_STATUS_COLORS.NON_RESIDENCE
		}
	];

	const onSubmit = handleSubmit((values: FormValues) => {
		console.log({ values });
	});

	return (
		<Card>
			<Space size="middle" align="center">
				<h3 className="mb-0">{t("Қават")}</h3>
				<Avatar shape="circle" className="f-center">
					1
				</Avatar>
			</Space>
			<Divider />
			<form onSubmit={onSubmit}>
				<Row justify="space-between" align="middle" className="mb-20">
					<Col>
						<Tooltip title={t("Жой қўшиш")}>
							<Button
								size="large"
								icon={<PlusCircleFilled />}
								className="f-center"
								onClick={() => append({})}
							/>
						</Tooltip>
					</Col>
					<Col>
						<StatusDots items={statusItems} size="middle" />
					</Col>
				</Row>

				<Row gutter={[30, 30]} className="mb-10">
					<Col lg={2}>№</Col>
					<Col lg={6}>
						<p className="mb-0">{t("Номери")}</p>
					</Col>
					<Col lg={5}>
						<p className="mb-0">{t("Тури")}</p>
					</Col>
					<Col lg={8}>
						<p className="mb-0">{t("Чизмаси")}</p>
					</Col>
				</Row>

				<div className={classes.formInner}>
					{!fields.length && <Empty description={t("Жой мавжуд эмас")} />}
					{fields.map(({ id }, idx) => (
						<Row key={id} gutter={[30, 30]} align="middle" className="mb-20">
							<Col lg={2}>{idx + 1}</Col>
							<Col lg={6}>
								<Controller
									name={`places.${idx}.num`}
									control={control}
									render={({ field }) => <Input {...field} />}
								/>
							</Col>
							<Col lg={5}>
								<Controller
									name={`places.${idx}.type`}
									control={control}
									render={({ field }) => (
										<Switch
											{...field}
											checkedChildren={t("Турар")}
											unCheckedChildren={t("Нотурар")}
											defaultChecked
											className="w100"
										/>
									)}
								/>
							</Col>
							<Col lg={8}>
								<Controller
									name={`places.${idx}.paint`}
									control={control}
									render={({ field }) => (
										<Select {...field} className="w100" />
									)}
								/>
							</Col>
							<Col lg={3}>
								<Button
									className="f-center"
									onClick={() => remove(idx)}
									danger
									icon={<Svg icon="trash" width={25} height={25} />}
									type="link"
								/>
							</Col>
						</Row>
					))}
				</div>
				<Divider />

				<Row className="mt-40" justify="center">
					<Button
						size="large"
						type="primary"
						shape="round"
						disabled={!fields}
						htmlType="submit"
						onClick={onSubmit}
					>
						{t("Сақлаш")}
						<DownCircleOutlined />
					</Button>
				</Row>
			</form>
		</Card>
	);
};

export default AddFloor;
