import { Col, Row } from "antd";
import { FC } from "react";
import { useTranslation } from "react-i18next";
import classes from "./CreateApartment.module.scss";
import { CreateApt, NonResidence, Residence } from "./components";
import { Divider } from "components/shared";

const CreateApartment: FC = () => {
	const { t } = useTranslation();

	return (
		<section className={classes.wrapper}>
			<Row gutter={[30, 30]} className="mb-20">
				<Col xs={24} lg={12}>
					<Residence />
				</Col>
				<Col xs={24} lg={12}>
					<NonResidence />
				</Col>
			</Row>

			<Divider />

			<CreateApt />
		</section>
	);
};

export default CreateApartment;
